import React, { useState, useRef, useCallback } from "react";
import { QRCodeSVG } from "qrcode.react";
import pako from "pako";

const iceServers = {
  iceServers: [{ urls: "stun:stun.l.google.com:19302" }],
};

// Uint8ArrayBase64
const uint8ArrayToBase64 = (array) => {
  return btoa(String.fromCharCode.apply(null, array));
};

// Base64Uint8Array
const base64ToUint8Array = (base64) => {
  const binaryString = atob(base64);
  const len = binaryString.length;
  const bytes = new Uint8Array(len);
  for (let i = 0; i < len; i++) {
    bytes[i] = binaryString.charCodeAt(i);
  }
  return bytes;
};

function ShareScreen() {
  const [offerUrl, setOfferUrl] = useState("");
  const [status, setStatus] = useState("...");
  const peerConnection = useRef(null);

  const startSharing = useCallback(async () => {
    try {
      setStatus("...");
      const stream = await navigator.mediaDevices.getDisplayMedia({
        video: true,
      });
      setStatus("...");

      peerConnection.current = new RTCPeerConnection(iceServers);

      stream.getTracks().forEach((track) => {
        peerConnection.current.addTrack(track, stream);
      });

      peerConnection.current.onicecandidate = (event) => {
        if (event.candidate === null) {
          const offerSdp = peerConnection.current.localDescription.sdp;
          const compressedSdp = pako.deflate(offerSdp);
          const encodedOffer = uint8ArrayToBase64(compressedSdp);

          const currentUrl = new URL(window.location.href);
          const pathSegments = currentUrl.pathname.split("/").filter((p) => p);
          if (pathSegments[pathSegments.length - 1] === "share") {
            pathSegments[pathSegments.length - 1] = "view";
          } else {
            pathSegments.push("view");
          }
          currentUrl.pathname = "/" + pathSegments.join("/");
          currentUrl.hash = encodedOffer;

          setOfferUrl(currentUrl.href);
          setStatus("QR");
        }
      };

      const offer = await peerConnection.current.createOffer();
      await peerConnection.current.setLocalDescription(offer);
    } catch (error) {
      console.error(":", error);
      setStatus("");
    }
  }, []);

  // --- :  ---
  // handleAnswerSubmitpako
  const handleAnswerSubmit = useCallback(async (encodedAnswer) => {
    try {
      // 1. Base64
      const compressedAnswer = base64ToUint8Array(encodedAnswer);
      // 2. SDP
      const answerSdp = pako.inflate(compressedAnswer, { to: "string" });

      const answer = new RTCSessionDescription({
        type: "answer",
        sdp: answerSdp,
      });
      await peerConnection.current.setRemoteDescription(answer);
      setStatus("");
    } catch (error) {
      console.error("Answer:", error);
      setStatus(`Answer: ${error.message}`);
    }
  }, []);
  // --- :  ---

  return (
    <div>
      <h1></h1>
      {!offerUrl ? (
        <div style={{ textAlign: "center" }}>
          <button onClick={startSharing} className="button primary-button">
            
          </button>
          <p className="status">{status}</p>
        </div>
      ) : (
        <>
          <div className="step">
            <h2 className="step-title">
              STEP 1: URLQR
            </h2>
            <p>
              URLQR
            </p>
            <textarea className="code-box" value={offerUrl} readOnly />
            <div className="qr-container">
              <QRCodeSVG value={offerUrl} size={256} />
            </div>
          </div>

          <div className="step">
            <h2 className="step-title">
              STEP 2: 
            </h2>
            <p>
              
            </p>
            <textarea
              className="code-box"
              placeholder="..."
              onPaste={(e) =>
                handleAnswerSubmit(e.clipboardData.getData("text"))
              }
            />
          </div>
          <p className="status">{status}</p>
        </>
      )}
    </div>
  );
}

export default ShareScreen;
